// one second
export const ONE_SECOND = 1e3

// one minute
export const ONE_MINUTE = 60 * 1e3

// one hour
export const ONE_HOUR = 60 * 60 * 1e3

// one day
export const ONE_DAY = 24 * 60 * 60 * 1e3

// one week
export const ONE_WEEK = 7 * 24 * 60 * 60 * 1e3



